*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


.App {
  height: 100vh;
  width: 100vh;
  padding-top: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: gray;
  text-shadow: 2px 5px 5px rgb(255, 36, 36);
}

h1 {
  margin-bottom: 10vh;
}

.main-container button {
  margin: 10px;
  height: 8vh;
  width: 14vh;
  border-radius: 10px;
  border: none;
  color: white;
  text-shadow: 2px 5px 5px black;
  background: rgb(169,247,233);
  background: radial-gradient(circle, rgba(169,247,233,1) 0%, rgba(99,155,221,1) 70%);
}
